*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

@font-face {
  font-family: Roboto;
  font-style: normal;
  font-weight: 400;
  src: url("roboto-v30-latin_cyrillic-regular.8f886f16.eot");
  src: local(""), url("roboto-v30-latin_cyrillic-regular.8f886f16.eot#iefix") format("embedded-opentype"), url("roboto-v30-latin_cyrillic-regular.9a4306a1.woff2") format("woff2"), url("roboto-v30-latin_cyrillic-regular.e4e2dc62.woff") format("woff"), url("roboto-v30-latin_cyrillic-regular.0b37ac96.ttf") format("truetype"), url("roboto-v30-latin_cyrillic-regular.dca3b2be.svg#Roboto") format("svg");
  font-display: swap;
}

@font-face {
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  src: url("roboto-v30-latin_cyrillic-500.078be101.eot");
  src: local(""), url("roboto-v30-latin_cyrillic-500.078be101.eot#iefix") format("embedded-opentype"), url("roboto-v30-latin_cyrillic-500.2139c97b.woff2") format("woff2"), url("roboto-v30-latin_cyrillic-500.0f23cff2.woff") format("woff"), url("roboto-v30-latin_cyrillic-500.a346a7e3.ttf") format("truetype"), url("roboto-v30-latin_cyrillic-500.ae933398.svg#Roboto") format("svg");
  font-display: swap;
}

body {
  font-family: DM Sans, sans-serif;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

button {
  cursor: pointer;
}

:root {
  --text-main-color: #000;
  --text-second-color: #fff;
  --text-footer-color: #545454;
  --text-error-color: #ff001b;
  --text-modal-color: #8c8c8c;
  --pre-accent-color: #ff6b01;
  --accent-color: #ff6b08;
  --header-bcg-color: rgba(0, 0, 0, .56);
  --main-bcg-color: #fff;
  --second-bcg-color: #f7f7f7;
  --third-bcg-color: #f7f7f7;
  --backdrop-bcg-color: rgba(0, 0, 0, .25);
  --bcg-color-modal: #f7f7f7;
  --card-shadow: 0px 4px 4px rgba(0, 0, 0, .25);
  --border-radius: 5px;
  --transition-time: 300ms;
  --transition-function: cubic-bezier(.4, 0, .2, 1);
}

.darkmode {
  --text-main-color: #fff;
  --text-second-color: #fff;
  --main-bcg-color: #000;
  --third-bcg-color: #000;
}

.visually-hidden {
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  position: absolute;
}

.btn {
  text-transform: uppercase;
  background: none;
  border-radius: 5px;
  font-family: Roboto;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.33;
  transition: background-color .3s cubic-bezier(.4, 0, .2, 1);
}

.container {
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 320px) {
  .container {
    width: 320px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1024px) {
  .container {
    width: 1024px;
  }
}

.section {
  text-align: center;
  padding-top: 20px;
  padding-bottom: 40px;
}

@media screen and (min-width: 768px) {
  .section {
    padding-top: 60px;
    padding-bottom: 60px;
  }
}

.page-navigation {
  width: 410px;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  display: flex;
}

@media screen and (max-width: 767px) {
  .page-navigation {
    width: 55vw;
    justify-content: space-between;
  }
}

.page-navigation > button {
  width: 40px;
  height: 40px;
  border-radius: var(--border-radius);
  cursor: pointer;
  background-color: var(--second-bcg-color);
  stroke: #000;
  border: none;
}

@media screen and (max-width: 767px) {
  .page-navigation > button {
    width: 20vw;
    height: 10vh;
  }
}

.page-navigation span {
  text-align: center;
  background-color: #fff;
  margin: 0 1px;
  padding-bottom: 8px;
}

.page-navigation span.active {
  color: var(--second-bcg-color);
}

.button-hidden {
  display: none;
}

.page-navigation-button__icon {
  fill-opacity: 0;
  stroke: inherit;
}

@media screen and (max-width: 767px) {
  .page-navigation-button__icon {
    width: 10vw;
    height: 5vh;
  }
}

.page-navigation-button__icon--rotate {
  transform: rotate(180deg);
}

.pages-list {
  align-items: center;
  display: flex;
}

@media screen and (max-width: 767px) {
  .pages-list {
    display: none;
  }
}

.pages-list > li {
  width: 40px;
  height: 40px;
  background-color: var(--second-bcg-color);
  border-radius: var(--border-radius);
  cursor: pointer;
  letter-spacing: normal;
  justify-content: center;
  align-items: center;
  margin: 1px;
  padding: 10px;
  font-family: Roboto;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.33;
  display: flex;
}

.pages-list > li.active {
  background-color: var(--accent-color);
  color: #fff;
}

.footer {
  background-color: var(--second-bcg-color);
  padding-top: 30px;
  padding-bottom: 30px;
}

.footer__info {
  text-align: center;
}

@media screen and (min-width: 768px) {
  .footer__info {
    justify-content: center;
    align-items: baseline;
    display: flex;
  }
}

@media screen and (max-width: 767px) {
  .footer__wrapper-rights {
    margin-bottom: 10px;
  }
}

@media screen and (min-width: 768px) {
  .footer__wrapper-rights {
    margin-right: 5px;
  }
}

.footer__wrapper-deveopers {
  justify-content: center;
  align-items: center;
  display: flex;
}

.footer__text {
  color: var(--text-footer-color);
  font-family: Roboto, sans-serif;
  font-size: 14px;
  line-height: 1.14;
}

.footer__copy, .footer__rights {
  border-right: 1px solid var(--text-footer-color);
  padding-right: 5px;
}

.footer__icon {
  margin: 0 10px;
}

.footer__link {
  color: var(--text-footer-color);
  text-decoration: underline;
}

.top-btn {
  z-index: 3;
  color: var(--text-second-color);
  transition: background-color var(--transition-time), var(--transition-function), opacity var(--transition-time), var(--transition-function);
  cursor: pointer;
  opacity: 0;
  visibility: hidden;
  background-color: #767d83;
  border: none;
  border-radius: 100px 0 0;
  outline: none;
  flex-direction: column;
  align-items: center;
  padding: 12px 12px 12px 25px;
  position: fixed;
  bottom: 0;
  right: 0;
}

.top-btn:hover {
  background-color: var(--accent-color);
}

.top-btn--is-show {
  opacity: 1;
  visibility: visible;
}

.top-btn__icon {
  width: 30px;
  fill: currentColor;
}

.gallery__image {
  border-radius: 5px;
  margin-bottom: 10px;
}

@media screen and (min-width: 320px) {
  .gallery__image {
    min-height: 420px;
  }
}

@media screen and (min-width: 768px) {
  .gallery__image {
    min-height: 455px;
  }
}

@media screen and (min-width: 1024px) {
  .gallery__image {
    min-height: 450px;
  }
}

.gallery__title, .gallery__genres, .gallery__date, .gallery__vote {
  letter-spacing: normal;
  font-family: Roboto;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.33;
}

.gallery__title {
  text-transform: uppercase;
  color: var(--text-main-color);
  transition: color var(--transition-time);
}

.gallery__genres, .gallery__date {
  color: var(--accent-color);
}

.gallery__date {
  margin-right: 8px;
}

.gallery__genres {
  border-right: 1px solid var(--accent-color);
  margin-right: 5px;
  padding-right: 5px;
}

.gallery__vote {
  color: var(--text-second-color);
  background-color: var(--accent-color);
  border-radius: 5px;
  padding: 1px 10px;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  margin-bottom: 40px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .gallery {
    margin-bottom: 60px;
  }
}

@media screen and (max-width: 767px) {
  .gallery__item:not(:last-child) {
    margin-bottom: 30px;
  }
}

@media screen and (min-width: 768px) {
  .gallery__item {
    flex-basis: calc(50% - 16px);
  }
}

@media screen and (min-width: 768px) and (max-width: 1023px) {
  .gallery__item:not(:nth-last-child(-n+2)) {
    margin-bottom: 32px;
  }

  .gallery__item:nth-child(2n+1) {
    margin-right: 32px;
  }
}

@media screen and (min-width: 1024px) {
  .gallery__item {
    flex-basis: calc(33.3333% - 11px);
    transition: transform .3s;
  }

  .gallery__item:not(:nth-last-child(-n+3)) {
    margin-bottom: 32px;
  }

  .gallery__item:not(:nth-child(3n)) {
    margin-right: 16.5px;
  }

  .gallery__item:hover {
    cursor: pointer;
    transition: transform .3s;
    transform: scale(1.05);
  }
}

.header {
  background-repeat: no-repeat;
  background-size: cover;
}

@media screen and (max-width: 767px) {
  .header {
    background-image: linear-gradient(to right, rgba(47, 48, 58, .4), rgba(47, 48, 58, .4)), url("header-background-mobile@1x.0ac3ba3c.jpg");
    padding-top: 40px;
    padding-bottom: 92px;
  }
}

@media screen and (min-width: 768px) {
  .header {
    background-image: linear-gradient(to right, rgba(47, 48, 58, .4), rgba(47, 48, 58, .4)), url("header-background-mobile@1x.0ac3ba3c.jpg");
  }
}

@media screen and (min-width: 1024px) {
  .header {
    background-image: linear-gradient(to right, rgba(47, 48, 58, .4), rgba(47, 48, 58, .4)), url("header-backgroung-desktop@2x.bc291aeb.jpg");
  }
}

@media screen and (min-width: 320px) {
  .header {
    padding-top: 40px;
    padding-bottom: 40px;
  }
}

.navigation {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.logo {
  transition: stroke .1s linear, transform .1s ease-in-out .2s;
  display: flex;
}

.logo:hover {
  transform: scale(1.1);
}

.logo__icon {
  width: 24px;
  display: inline-block;
}

.logo__link {
  color: var(--text-second-color);
  font-family: Roboto;
  font-size: 30px;
  font-weight: 500;
  text-decoration: none;
}

.logo__link:last-child {
  margin-left: 10px;
}

@media screen and (max-width: 767px) {
  .logo__link--notmobile {
    display: none;
  }
}

.nav__list {
  display: flex;
}

.nav__item:not(:last-child) {
  margin-right: 40px;
}

.nav__link {
  color: var(--text-second-color);
  font-family: Roboto;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  text-decoration: none;
  transition: background-color .3s cubic-bezier(.4, 0, .2, 1);
  display: block;
}

.nav__link:hover {
  color: #d41443;
}

.nav__link--default {
  border-bottom: 3px solid #ff6b08;
}

.search {
  justify-content: center;
}

@media screen and (min-width: 320px) {
  .search {
    margin-top: 55px;
  }
}

@media screen and (max-width: 767px) {
  .search {
    margin-top: 40px;
  }
}

.search__form {
  width: 300px;
  border-bottom: .5px solid #fff;
  margin: 0 auto;
  transition: border-bottom .5s linear;
  position: relative;
}

@media screen and (max-width: 767px) {
  .search__form {
    width: auto;
  }
}

.search__form:hover, .search__form:focus {
  border-color: #ff6b01;
}

.search__input, .search__button {
  background: none;
  border: none;
  outline: none;
}

.search__input {
  color: var(--text-second-color);
  width: 100%;
}

.search__button {
  cursor: pointer;
  padding: 0;
  position: absolute;
  top: 0;
  right: 0;
}

::-ms-input-placeholder {
  color: var(--text-second-color);
  font-family: Roboto;
  font-size: 14px;
  font-weight: 400;
  line-height: 16px;
}

::placeholder {
  color: var(--text-second-color);
  font-family: Roboto;
  font-size: 14px;
  font-weight: 400;
  line-height: 16px;
}

.header__library {
  background-repeat: no-repeat;
  background-size: cover;
}

@media screen and (max-width: 767px) {
  .header__library {
    background-image: linear-gradient(to right, rgba(47, 48, 58, .4), rgba(47, 48, 58, .4)), url("header-library-background-mobile@1x.f545a430.jpg");
    padding-top: 55px;
    padding-bottom: 92px;
  }
}

@media screen and (min-width: 768px) {
  .header__library {
    background-image: linear-gradient(to right, rgba(47, 48, 58, .4), rgba(47, 48, 58, .4)), url("header-library-background-tablet@1x.5570b90a.jpg");
  }
}

@media screen and (min-width: 1024px) {
  .header__library {
    background-image: linear-gradient(to right, rgba(47, 48, 58, .4), rgba(47, 48, 58, .4)), url("header-library-background-desktop@1x.3570cff6.jpg");
  }
}

@media screen and (min-width: 320px) {
  .header__library {
    padding-top: 40px;
    padding-bottom: 40px;
  }
}

.library__button-wrap {
  justify-content: center;
  display: flex;
}

.button-wrap__button {
  width: 148px;
  height: 42px;
  color: var(--text-second-color);
  background: none;
  border: 1px solid;
  margin-top: 64px;
  font-family: Roboto;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 16px;
}

@media screen and (min-width: 768px) and (max-width: 1023px) {
  .button-wrap__button {
    width: 152px;
    height: 44px;
  }
}

@media screen and (max-width: 767px) {
  .button-wrap__button {
    width: 130px;
    height: 44px;
  }
}

.button-wrap__button:hover {
  background-color: #f11d1d;
}

.button-wrap__button--active {
  background: #ff6b01;
  border: none;
  border-radius: 5px;
}

.button-wrap__button:last-child {
  margin-left: 15px;
}

@media screen and (min-width: 768px) and (max-width: 1023px) {
  .button-wrap__button:last-child {
    margin-left: 32px;
  }
}

@media screen and (max-width: 767px) {
  .button-wrap__button:last-child {
    margin-left: 20px;
  }
}

.library__btn--active {
  background: var(--pre-accent-color);
  filter: drop-shadow(0 8px 43px rgba(255, 107, 1, .6));
  border: none;
}

.header__container {
  position: relative;
}

.auth {
  position: absolute;
  top: 40px;
  right: 30px;
}

@media screen and (max-width: 767px) {
  .auth {
    right: 20px;
  }
}

.auth__container {
  flex-direction: column;
  align-items: flex-end;
  display: flex;
}

.auth__greeting {
  color: var(--text-second-color);
  margin-bottom: 5px;
  margin-right: 5px;
  font-family: Roboto;
  font-size: 14px;
  font-weight: 500;
  line-height: 16px;
}

.auth__username {
  color: var(--text-second-color);
  margin-left: 5px;
  font-family: Roboto;
  font-size: 14px;
  font-style: italic;
  font-weight: 500;
  line-height: 16px;
}

.auth__container > div {
  justify-content: flex-end;
  align-items: baseline;
  display: flex;
}

.auth__signIn, .auth__signUp, .auth__signOut {
  color: var(--text-second-color);
  cursor: pointer;
  background: none;
  border: 1px solid;
  border-radius: 5px;
  font-family: Roboto;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 16px;
  transition: background-color .3s cubic-bezier(.4, 0, .2, 1);
}

.auth__signIn {
  margin-right: 8px;
}

.auth__signIn + span {
  color: var(--text-second-color);
  margin-right: 8px;
  font-family: Roboto;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 16px;
}

body.team-modal-is-open {
  overflow: hidden;
}

.team__container {
  background-color: var(--text-second-color);
  width: 300px;
  max-width: 528px;
  max-height: 100vh;
  color: var(--text-main-color);
  padding: 30px 20px;
  font-size: 16px;
  overflow-y: scroll;
}

@media screen and (min-width: 768px) and (max-width: 1023px) {
  .team__container {
    width: 628px;
    height: 628px;
    margin: auto;
    padding: 30px;
  }
}

@media screen and (min-width: 1024px) {
  .team__container {
    min-width: 900px;
    height: 700px;
    padding: 30px;
  }
}

.ref__readme {
  color: var(--text-main-color);
  list-style: none;
}

.title-wrapper {
  justify-content: center;
  margin-top: 20px;
  margin-bottom: 20px;
  display: flex;
}

@media screen and (min-width: 768px) and (max-width: 1023px) {
  .title-wrapper {
    margin-top: 30px;
    margin-bottom: 30px;
  }
}

@media screen and (min-width: 1024px) {
  .title-wrapper {
    margin-top: 30px;
    margin-bottom: 30px;
  }
}

.team__title {
  font-size: 20px;
}

.team__title:hover {
  color: var(--accent-color);
  transition: color var(--transition-time) var(--transition-function);
}

@media screen and (min-width: 768px) and (max-width: 1023px) {
  .team__title {
    margin-bottom: 20px;
    line-height: 1.16;
  }
}

@media screen and (min-width: 768px) and (max-width: 1023px) and (min-width: 1024px) {
  .team__title {
    margin-top: 10px;
    margin-bottom: 40px;
    font-size: 28px;
  }
}

.under {
  color: var(--pre-accent-color);
}

.under:hover {
  color: var(--text-main-color);
}

.goitLogo {
  height: 24px;
  width: 65px;
  margin-right: 10px;
}

@media screen and (min-width: 768px) and (max-width: 1023px) {
  .goitLogo {
    height: 23px;
    width: 65px;
    margin-right: 15px;
  }
}

@media screen and (min-width: 1024px) {
  .goitLogo {
    height: 24px;
    width: 80px;
    margin-right: 15px;
  }
}

.team__content {
  text-align: center;
}

.team__list {
  flex-wrap: wrap;
  display: flex;
}

.team__item {
  width: calc(50% - 10px);
  border-radius: 0 0 4px 4px;
  margin-bottom: 15px;
  margin-right: auto;
}

@media screen and (max-width: 1023px) {
  .team__item:first-child {
    text-align: center;
  }

  .team__item:nth-child(2n) {
    margin-right: 5px;
